* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

.search-form {
  z-index: 999;
  background-color: #4056b4;
  justify-content: center;
  gap: 10px;
  width: 100%;
  padding: 20px;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.search-form input {
  border: none;
  border-radius: 4px;
  outline: none;
  width: 300px;
  padding: 8px;
}

.search-form button {
  cursor: pointer;
  background-color: #fff;
  border: none;
  border-radius: 4px;
  padding: 8px 16px;
  transition: background-color .3s;
}

.search-form button:hover {
  background-color: #e0e0e0;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  padding: 80px 20px 20px;
  display: flex;
}

.photo-card {
  width: 300px;
  color: inherit;
  border-radius: 4px;
  text-decoration: none;
  transition: transform .3s;
  overflow: hidden;
  box-shadow: 0 1px 3px #0000001f;
}

.photo-card:hover {
  transform: scale(1.03);
}

.photo-card img {
  object-fit: cover;
  width: 100%;
  height: 200px;
}

.info {
  text-align: center;
  background-color: #fff;
  justify-content: space-around;
  padding: 10px;
  display: flex;
}

.info-item {
  flex-direction: column;
  align-items: center;
  margin: 0;
  font-size: 14px;
  display: flex;
}

.info-item b {
  margin-bottom: 5px;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #4056b4;
  border: none;
  border-radius: 4px;
  margin: 20px auto;
  padding: 10px 20px;
  transition: background-color .3s;
  display: none;
}

.load-more:hover {
  background-color: #3045a3;
}

.load-more:disabled {
  cursor: not-allowed;
  background-color: #ccc;
}
